.possibility {
  display: flex;
  flex-direction: row;
  /* justify-content: space-between;
  align-items: center; */
}

.possibility-image {
  flex: 1;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-left: 2rem;
}

.possibility-image img {
  width: 100%;
  height: 100%;
}

.possibility-content {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: flex-start;
}

.possibility-content h4 {
  font-family: var(--font-family);
  font-weight: 500;
  font-size: 16px;
  line-height: 30px;
  color: #71e5ff;
}

.possibility-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 34px;
  line-height: 45px;
  margin: 1rem 0;
}

.possibility-content p {
  font-family: var(--font-family);
  font-weight: 400;
  font-size: 16px;
  line-height: 30px;
  color: var(--color-text);
}

@media screen and (max-width: 905px) {
  .possibility {
    flex-direction: column;
  }

  .possibility-image {
    margin: 1rem 0;
  }

  .possibility-content {
    margin-top: 2rem;
  }
}
